"use client";

import { useState } from "react";

export default function PetCard() {
  const [pet, setPet] = useState({
    hunger: 50,
    energy: 40,
    happiness: 60,
    fun: 50,
    level: 1,
  });

  const feed = () => setPet(prev => ({ ...prev, hunger: Math.max(prev.hunger - 10, 0) }));
  const play = () => setPet(prev => ({ ...prev, happiness: Math.min(prev.happiness + 10, 100), fun: Math.min(prev.fun + 10, 100) }));
  const sleep = () => setPet(prev => ({ ...prev, energy: Math.min(prev.energy + 15, 100) }));
  const bath = () => setPet(prev => ({ ...prev, happiness: Math.min(prev.happiness + 5, 100) }));

  return (
    <div style={{ background: '#222', color: '#fff', padding: '20px', borderRadius: '12px', width: '300px', margin: '50px auto' }}>
      <h2>üê∂ My Digital Pet</h2>

      <StatusBar label="Hunger" value={pet.hunger} />
      <StatusBar label="Energy" value={pet.energy} />
      <StatusBar label="Happiness" value={pet.happiness} />
      <StatusBar label="Fun" value={pet.fun} />
      <StatusBar label="Level" value={pet.level * 20} />

      <div style={{ display: 'flex', justifyContent: 'space-between', marginTop: '20px' }}>
        <button onClick={feed}>Feed</button>
        <button onClick={play}>Play</button>
        <button onClick={sleep}>Sleep</button>
        <button onClick={bath}>Bath</button>
      </div>
    </div>
  );
}

function StatusBar({ label, value }: { label: string; value: number }) {
  return (
    <div style={{ marginBottom: '10px' }}>
      <span>{label}: </span>
      <progress value={value} max={100} style={{ width: '100%' }} />
      <span> {value}%</span>
    </div>
  );
}
